{/* Mobile Menu Overlay */}
{isMobileMenuOpen && (
  <div className="fixed inset-0 z-50 bg-black/60 md:hidden">
    {/* Slide panel */}
    <div className="fixed top-0 right-0 h-full w-72 bg-background shadow-lg animate-slide-in">
      
      {/* Header */}
      <div className="flex items-center justify-between px-6 py-4 border-b">
        <span className="text-lg font-bold">Sagar Almad</span>
        <button onClick={() => setIsMobileMenuOpen(false)}>
          <X className="h-6 w-6" />
        </button>
      </div>

      {/* Links */}
      <nav className="flex flex-col gap-6 px-6 py-8 text-base">
        {navLinks.map((link) => (
          <a
            key={link.href}
            href={link.href}
            onClick={() => setIsMobileMenuOpen(false)}
            className="text-muted-foreground hover:text-primary transition"
          >
            {link.label}
          </a>
        ))}
      </nav>
    </div>
  </div>
)}
